<template>
  <section class="section" id="ilmoittautuminen">
    <div class="container content">
      <h1 class="title">ILMOITTAUTUMINEN</h1>

      <h4>Illalliskortin hinta:</h4>
      <div class="columns">
        <div class="column is-two-fifths">
          <table class="table is-striped">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>Opiskelijat</th>
                <td>alkoholillinen</td>
                <td>100,00 €</td>
              </tr>
              <tr>
                <th>Opiskelijat</th>
                <td>alkoholiton</td>
                <td>95,00 €</td>
              </tr>
              <tr>
                <th>Muut</th>
                <td>alkoholillinen</td>
                <td>110,00 €</td>
              </tr>
              <tr>
                <th>Muut</th>
                <td>alkoholiton</td>
                <td>105,00 €</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <p>Silliaamiainen 10,00 €</p>
      <transition name="fade" mode="out-in">
        <div v-if="nowDate >= startDate && nowDate <= endDate">
          <form @submit.prevent="onSubmit">
            <div class="field">
              <label class="label">Etu- ja sukunimi</label>
              <div class="control">
                <input class="input" type="text" v-model="name" placeholder="Etu- ja sukunimi">
              </div>
            </div>
            <div class="field">
              <label class="label">Ryhmä</label>
              <div class="control">
                <div class="select">
                  <select v-model="group">
                    <option>Opiskelija</option>
                    <option>Alumni</option>
                    <option>Kutsuvieras</option>
                  </select>
                </div>
              </div>
            </div>
            <div class="field">
              <label class="label">Sähköposti</label>
              <div class="control">
                <input class="input" type="email" v-model="email" placeholder="Sähköposti">
              </div>
            </div>
            <div class="field">
              <div class="control">
                <button class="button is-link">Ilmoittaudu</button>
              </div>
            </div>
          </form>
        </div>
        <div v-else-if="nowDate <= startDate">
          <p class="mb-4 has-text-weight-medium">Ilmoittautuminen alkaa xx.xx.2021</p>
        </div>
        <div v-else-if="nowDate >= endDate">
          <p class="mb-4 has-text-weight-medium">Ilmoittautuminen on päättynyt.</p>
        </div>
      </transition>
      <div v-if="registered">
        {{ registered }}
      </div>
    </div>
  </section>
</template>

<script>
  import axios from 'axios';

  export default {
      name: 'Index',
      data() {
        return {
          nowDate: new Date().getTime(),
          startDate: new Date("Jan 23, 2022 10:14:10").getTime(),
          endDate: new Date("Jan 23, 2022 10:14:30").getTime(),
          name: '',
          group: '',
          email: '',
          registered: null
        }
      },
      mounted () {
        //this.getRegistrations();
        setInterval(() => {
          this.nowDate = new Date().getTime();
        }, 1000);
      },
      methods: {
        onSubmit() {
          let registration = {
            name: this.name,
            group: this.group,
            email: this.email,
          }
          axios
            .post('http://localhost:5000/api/v1/registrations', registration)
            .then(response => {
              console.log(response);
              this.getRegistrations();
            })
            .catch(error => console.log(error))

          this.name = '';
          this.group = '';
          this.email = '';
        },
        getRegistrations() {
          axios
            .get('http://localhost:5000/api/v1/registrations')
            .then(response => (this.registered = response.data))
            .catch(error => console.log(error))
        }
      }
  }
</script>

